import React from 'react'
import Container from "../components/container"

const NotFoundPage = () => (
  <div>
    <Container>
      <h1>404 NOT FOUND</h1>
      <p>The link you are trying to access doesn't exist! :(</p>
    </Container>
  </div>
)

export default NotFoundPage



